/* Global Styles */
body {
  font-family: Arial, sans-serif;
  margin: 0;
  padding: 0;
  background-color: #f4f4f4;
}

/* Container for the entire app */
.App {
  text-align: center;
  padding: 20px;
}

/* Header Styles */
h1 {
  color: #333;
  font-size: 2.5em;
}

/* Filters Section Styles */
.filters {
  margin: 20px 0;
}

.filters input {
  margin: 5px;
  padding: 10px;
  border-radius: 5px;
  border: 1px solid #ccc;
}

/* Expense Form Styles */
.expense-form {
  margin: 20px 0;
}

.expense-form input {
  margin: 5px;
  padding: 10px;
  border-radius: 5px;
  border: 1px solid #ccc;
}

.expense-form button {
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  background-color: #007bff;
  color: white;
  cursor: pointer;
}

.expense-form button:hover {
  background-color: #0056b3;
}

/* Chart Container Styles */
.chart-container {
  margin: 20px 0;
}

/* Expense Table Styles */
.expense-table {
  margin: 20px 0;
}

table {
  width: 100%; /* Full width of its container */
  border-collapse: collapse; /* Ensures borders are collapsed into a single border */
  margin: 20px auto; /* Centers the table horizontally and adds some margin */
}

th, td {
  border: 1px solid #ddd; /* Light grey border around table cells */
  padding: 8px; /* Adds padding to table cells */
  text-align: left; /* Aligns text to the left */
}

th {
  border-bottom: 4px solid #000; /* Thick bottom border for headers */
  background-color: #f4f4f4; /* Light grey background for headers */
}

tr:nth-child(even) {
  background-color: #f9f9f9; /* Light grey background for even rows */
}

tr:nth-child(odd) {
  background-color: #ffffff; /* White background for odd rows */
}

/* Additional styles can be added below */
